.grid-container {
  display: flex;
  .column {
    width: 100%;
    display: flex;
    flex-direction: column;
    text-align: center;
  }
}

.grid-square {
  border: 0.5px solid rgb(200,200,200);
  content: ' ';
  padding: 1em 0;

  &.sweeped {
    background-color: rgb(190,190,190);
  }

  &.flagged {
    background-color: blue;
  }

  &.mine.sweeped {
    background-color: red;
    content: 'x';
  }
}

