.start-view-container {
  &>p {
    text-align: center;
  }

  .controller {
    display: flex;
    flex-direction: column;

    &>div {
      display: flex;
      flex-direction: column;

      label {
        font-size: 1.4em;
      }
    }
  }

  .btn-container {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .btn {
    padding: 1em 2em;
    margin-top: 1.5em;
  }
  .btn:hover {
    cursor: pointer;
    background-color: rgb(200,200,200);
  }
}